@import "../utils/variables";

.filter {
  &__item {
    color: $primary-gray-color;
    padding: 10px 16px;
    cursor: pointer;
    transition: box-shadow 0.2s ease;

    &:hover {
      box-shadow: 0 3px 8px rgba(0,0,0,0.25), 0 3px 3px rgba(0,0,0,0.22);
    }

    &:active {
      opacity: 0.8;
    }
  }
}
